<template>
  <div>삭제중...</div>
</template>

<script>
import http from '@/util/http-common';
export default {
  name: 'replydelete',
data() {
    return {
        item: {}
    }
},

  created() {
      // 일단 한번 더 가져온 다음, 질문 부분만 남기고 삭제
    http.get(`/qna/${this.$route.query.no}`).then(({ data }) => {

      this.item.qnaNo = data.qnaNo;
      this.item.qnaUserid = data.qnaUserid;
      this.item.qnaTitle = data.qnaTitle;
      this.item.qnaContent = data.qnaContent;
      this.item.qnaDatetime = data.qnaDatetime;

        http
        .put(`/qna/${this.qnaNo}`, this.item)
        .then(({ data }) => {
          let msg = '삭제 처리시 문제가 발생했습니다.';
          if (data === 'success') {
            msg = '답변 삭제가 완료되었습니다.';
          }
          alert(msg);
          this.$router.push('/read?no=' + this.item.qnaNo);
        })
        .catch(() => {
          alert('삭제 처리시 에러가 발생했습니다.');
        });
    });
  },
};
</script>

