<template>
  <div>
    <div class="form-group">
      <label for="replyUserid">작성자</label>
      <input
        type="text"
        class="form-control"
        id="replyUserid"
        ref="replyUserid"
        placeholder="작성자를 입력하세요"
        v-model="replyUserid"
        readonly
      />
    </div>

    <div class="form-group">
      <label for="replyContent">내용</label>
      <textarea
        type="text"
        class="form-control"
        id="content"
        ref="replyContent"
        placeholder="내용을 입력하세요"
        v-model="replyContent"
      ></textarea>
    </div>
    <div class="text-right">
      <button
        class="btn btn-primary"
        v-if="type == 'create'"
        @click="checkHandler"
      >
        등록
      </button>
      <button class="btn btn-primary" v-else @click="checkHandler">수정</button>
      <button class="btn btn-primary" @click="moveList">목록</button>
    </div>
  </div>
</template>

<script>
import http from '@/util/http-common';
export default {
  name: 'board-Form',
  props: {
    type: { type: String }, // create.vue나 update.vue에서 props로 type을 받는다. 이 type에 따라서 입력폼 또는 수정폼이 됨
  },
  data: function() {
    return {
      qnaNo: '',
      qnaTitle: '',
      qnaContent: '',
      qnaUserid: '',
      qnaDatetime: '',
      replyDatetime: '',
      replyUserid: '',
      replyContent: '',
    };
  },

  methods: {
    getUserInfo(){
      http
        .get('/user')
        .then(({ data }) => {
          this.replyUserid = data.id;
        })
        .catch(() => {
          alert('에러가 발생했습니다.');
        });
    },

    checkHandler() {
      let err = true;
      let msg = '';
      !this.replyUserid &&
        ((msg = '작성자를 입력해주세요'),
        (err = false),
        this.$refs.replyUserid.focus());
      err &&
        !this.replyContent &&
        ((msg = '내용 입력해주세요'),
        (err = false),
        this.$refs.replyContent.focus());

      if (!err) alert(msg);
      else {
        this.replyDatetime = new Date();
        this.type == 'create' ? this.createHandler() : this.updateHandler();
      }
    },
    createHandler() {
      http
        .put(`/qna/${this.qnaNo}`, {
          qnaNo: this.qnaNo,
          qnaTitle: this.qnaTitle,
          qnaContent: this.qnaContent,
          qnaUserid: this.qnaUserid,
          qnaDatetime: this.qnaDatetime,
          replyDatetime: this.replyDatetime,
          replyUserid: this.replyUserid,
          replyContent: this.replyContent,
        })
        .then(({ data }) => {
          let msg = '등록 처리시 문제가 발생했습니다.';
          if (data === 'success') {
            msg = '등록이 완료되었습니다.';
          }
          alert(msg);
          this.moveList();
        })
        .catch(() => {
          alert('등록 처리시 에러가 발생했습니다.');
        });
    },
    updateHandler() {
      http
        .put(`/qna/${this.qnaNo}`, {
          qnaNo: this.qnaNo,
          qnaTitle: this.qnaTitle,
          qnaContent: this.qnaContent,
          qnaUserid: this.qnaUserid,
          qnaDatetime: this.qnaDatetime,
          replyDatetime: this.replyDatetime,
          replyUserid: this.replyUserid,
          replyContent: this.replyContent,
        })
        .then(({ data }) => {
          let msg = '수정 처리시 문제가 발생했습니다.';
          if (data === 'success') {
            msg = '수정이 완료되었습니다.';
          }
          alert(msg);
          this.moveList();
        })
        .catch(() => {
          alert('수정 처리시 에러가 발생했습니다.');
        });
    },
    moveList() {
      this.$router.push('/list');
    },
  },
  created() {
    //if (this.type === 'update') {
      http
        .get(`/qna/${this.$route.query.no}`)
        .then(({ data }) => {
          this.qnaNo = data.qnaNo;
          this.qnaTitle = data.qnaTitle;
          this.qnaContent = data.qnaContent;
          this.qnaUserid = data.qnaUserid;
          this.qnaDatetime = data.qnaDatetime;
          this.replyDatetime = data.replyDatetime;
          this.replyUserid = data.replyUserid;
          this.replyContent = data.replyContent;


        this.getUserInfo();

        })
        .catch(() => {
          alert('에러가 발생했습니다.');
        });
    //}
  },
};
</script>

<style scoped></style>
