<template>
  <div>
    <div class="form-group">
      <label for="qnaUserid">작성자</label>
      <input
        type="text"
        class="form-control"
        id="qnaUserid"
        ref="qnaUserid"
        placeholder="작성자를 입력하세요"
        v-model="qnaUserid"
        readonly
      />
    </div>
    <div class="form-group">
      <label for="qnaTitle">제목</label>
      <input
        type="text"
        class="form-control"
        id="qnaTitle"
        ref="qnaTitle"
        placeholder="제목을 입력하세요"
        v-model="qnaTitle"
      />
    </div>
    <div class="form-group">
      <label for="qnaContent">내용</label>
      <textarea
        type="text"
        class="form-control"
        id="contnet"
        ref="qnaContent"
        placeholder="내용을 입력하세요"
        v-model="qnaContent"
      ></textarea>
    </div>
    <div class="text-right">
      <button
        class="btn btn-primary"
        v-if="type == 'create'"
        @click="checkHandler"
      >
        등록
      </button>
      <button class="btn btn-primary" v-else @click="checkHandler">수정</button>
      <button class="btn btn-primary" @click="moveList">목록</button>
    </div>
  </div>
</template>

<script>
import http from '@/util/http-common';
import moment from 'moment';
export default {
  name: 'board-Form',
  props: {
    type: { type: String }, // create.vue나 update.vue에서 props로 type을 받는다. 이 type에 따라서 입력폼 또는 수정폼이 됨
  },
  data: function() {
    return {
      qnaNo: '',
      qnaTitle: '',
      qnaContent: '',
      qnaUserid: '',
      qnaDatetime: '',
      replyDatetime: '',
      replyUserid: '',
      replyContent: '',
    };
  },

  methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format('YYYY-MM-DD HH:mm:ss');
    },

    getUserInfo(){
      http
        .get('/user')
        .then(({ data }) => {
          this.qnaUserid = data.id;
        })
        .catch(() => {
          alert('에러가 발생했습니다.');
        });
    },

    checkHandler() {
      let err = true;
      let msg = '';
      !this.qnaUserid &&
        ((msg = '작성자를 입력해주세요'),
        (err = false),
        this.$refs.qnaUserid.focus());
      err &&
        !this.qnaTitle &&
        ((msg = '제목 입력해주세요'), (err = false), this.$refs.qnaTitle.focus());
      err &&
        !this.qnaContent &&
        ((msg = '내용 입력해주세요'),
        (err = false),
        this.$refs.qnaContent.focus());

      if (!err) alert(msg);
      else {
        this.qnaDatetime = new Date();
        this.type == 'create' ? this.createHandler() : this.updateHandler();
      }
    },
    createHandler() {
      http
        .post('/qna', {
          qnaUserid: this.qnaUserid,
          qnaTitle: this.qnaTitle,
          qnaContent: this.qnaContent,
          qnaDatetime: this.qnaDatetime
        })
        .then(({ data }) => {
          let msg = '등록 처리시 문제가 발생했습니다.';
          if (data === 'success') {
            msg = '등록이 완료되었습니다.';
          }
          alert(msg);
          this.moveList();
        })
        .catch(() => {
          alert('등록 처리시 에러가 발생했습니다.');
        });
    },
    updateHandler() {
      http
        .put(`/qna/${this.no}`, {
          qnaNo: this.qnaNo,
          qnaDatetime: this.qnaDatetime,
          qnaUserid: this.qnaUserid,
          qnaTitle: this.qnaTitle,
          qnaContent: this.qnaContent,
          // 수정할땐 달려있던 답글까지 같이 안 넣어주면 답글이 날아가버린다
          replyDatetime: this.replyDatetime,
          replyUserid: this.replyUserid,
          replyContent: this.replyContent,
        })
        .then(({ data }) => {
          let msg = '수정 처리시 문제가 발생했습니다.';
          if (data === 'success') {
            msg = '수정이 완료되었습니다.';
          }
          alert(msg);
          this.moveList();
        })
        .catch(() => {
          alert('수정 처리시 에러가 발생했습니다.');
        });
    },
    moveList() {
      this.$router.push('/list');
    },
  },
  created() {

    if (this.type === 'update') {
      http
        .get(`/qna/${this.$route.query.no}`)
        .then(({ data }) => {
          this.qnaNo = data.qnaNo;
          this.qnaDatetime = data.qnaDatetime;
          this.qnaUserid = data.qnaUserid;
          this.qnaTitle = data.qnaTitle;
          this.qnaContent = data.qnaContent;
          this.replyDatetime = data.replyDatetime;
          this.replyUserid = data.replyUserid;
          this.replyContent = data.replyContent;
        })
        .catch(() => {
          alert('에러가 발생했습니다.');
        })
        .finally(() => {
          this.getUserInfo();
        })

    }
    else{
      this.getUserInfo();
    }
  },
};
</script>

<style scoped></style>
