import Vue from 'vue';
import VueRouter from 'vue-router';
import Index from '@/page/index.vue';
import List from '@/page/list.vue';
import Create from '@/page/create.vue';
import Read from '@/page/read.vue';
import Update from '@/page/update.vue';
import Delete from '@/page/delete.vue';

import Reply from '@/page/reply.vue';
import ReplyUpdate from '@/page/replyupdate.vue';
import ReplyDelete from '@/page/replydelete.vue';

Vue.use(VueRouter);
export const router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/reply',
      name: 'reply',
      component: Reply,
    },
    {
      path: '/replyupdate',
      name: 'replyupdate',
      component: ReplyUpdate,
    },
    {
      path: '/replydelete',
      name: 'replydelete',
      component: ReplyDelete,
    },
    {
      path: '/',
      name: 'index',
      component: Index, // 이걸 List로 바꿔서 초기화면을 리스트로...?
    },
    {
      path: '/list',
      name: 'list',
      component: List,
    },
    {
      path: '/create',
      name: 'create',
      component: Create,
    },
    {
      path: '/read',
      name: 'read',
      component: Read,
    },
    {
      path: '/update',
      name: 'update',
      component: Update,
    },
    {
      path: '/delete',
      name: 'delete',
      component: Delete,
    },
  ],
});
