<template>
  <div>
    <img src="@/assets/Q.png" width="50px" height="50px">
    <table class="table table-bordered w-50">
      <tr>
        <th>번호</th>
        <td>{{ qnaNo }}</td>
      </tr>
      <tr>
        <th>글쓴이</th>
        <td>{{ qnaUserid }}</td>
      </tr>
      <tr>
        <th>제목</th>
        <td>{{ qnaTitle }}</td>
      </tr>
      <tr>
        <th>날짜</th>
        <td>{{ getFormatDate(qnaDatetime) }}</td>
      </tr>

      <tr>
        <td colspan="2">
          {{ qnaContent }}
        </td>
      </tr>
    </table>

    <img src="@/assets/A.png" width="50px" height="50px">
    <table v-if="replyUserid != null" class="table table-bordered w-50">
      <tr>
        <th>글쓴이</th>
        <td>{{ replyUserid }}</td>
      </tr>
      <tr>
        <th>날짜</th>
        <td>{{ getFormatDate(replyDatetime) }}</td>
      </tr>
      <tr>
        <td colspan="2">
          {{ replyContent }}
        </td>
      </tr>
    </table>
    <div v-else>아직 답변이 없습니다.</div>

    <br />
    <div class="text-center">
      <router-link to="/list"
        ><button class="btn btn-primary">목록</button></router-link
      >
      <router-link :to="'/update?no=' + qnaNo" v-if="qnaUserid == userData.id"
        ><button class="btn btn-primary">질문 수정</button></router-link
      >
      
      <router-link :to="'/delete?no=' + qnaNo" v-if="qnaUserid == userData.id"
        ><button class="btn btn-primary">질문 삭제</button></router-link
      >

      <router-link :to="'/reply?no=' + qnaNo" v-if="(replyUserid == null) && (userData.isAdmin == true)"
        ><button class="btn btn-primary">답변하기</button></router-link
      >

      <router-link :to="'/replyupdate?no=' + qnaNo" v-if="replyUserid == userData.id"
        ><button class="btn btn-primary">답변 수정</button></router-link
      >

      <router-link :to="'/replydelete?no=' + qnaNo" v-if="replyUserid == userData.id"
        ><button class="btn btn-primary">답변 삭제</button></router-link
      >

    </div>
  </div>
</template>

<script>
import moment from 'moment';
import http from '@/util/http-common';
export default {
  name: 'detail',
  props: {
    qnaNo: { type: Number },
    qnaUserid: { type: String },
    qnaTitle: { type: String },
    qnaContent: { type: String },
    qnaDatetime: { type: String },
    replyContent: { type: String },
    replyDatetime: { type: String },
    //loginUserid: { type: String },
    replyUserid: { type: String },
  },
  data() {
    return {
      userData: {}
    }
  },

created() {
      http
        .get('/user')
        .then(({ data }) => {
          this.userData = data;
          console.log(this.userData);
        })
        .catch(() => {
          alert('에러가 발생했습니다.');
        });
},

  methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format('YYYY.MM.DD HH:mm:ss');
    },
  },
};
</script>
